 }


 @keyframes backgroundColorChange {
  0% {
    background-color: #ff5733; /* Color 1 */
  }
  25% {
    background-color: #33ff57; /* Color 2 */
  }
  50% {
    background-color: #5733ff; /* Color 3 */
  }
  75% {
    background-color: #ff5733; /* Color 4 */
  }
  100% {
    background-color: #33ff57; /* Color 5 */
  }
}

